<!--  -->
<template>
<div>
  <el-tabs @tab-click="handleClick">
    
    <el-tab-pane label="Retrieve by month">
      <div class="block">
    <el-date-picker
      v-model="month"
      type="month"
      placeholder="Choose a month">
    </el-date-picker>
    </div>

    <div v-if="bookings.length!=0" >    
    <el-card>
        <el-descriptions class="margin-top"  :column="3"  v-for="booking in displayedItems" :key="booking.index" border
        >
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-user"></i>
        Name
      </template>
      {{booking.firstName}}{{booking.lastName}}
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-mobile-phone"></i>
        Phone Number
      </template>
      {{booking.phoneNumber}}
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-location-outline"></i>
        Address
      </template>
      {{ booking.address }}
    </el-descriptions-item>
    <el-descriptions-item v-if="booking.sedanServices.length!=0">
      <template slot="label">
        <i class="el-icon-tickets"></i>
        Sedan Services
      </template>
      <span v-for="s in booking.sedanServices" :key="s.index">{{ s }}</span>
    </el-descriptions-item>
    <el-descriptions-item v-if="booking.suvServices.length!=0">
      <template slot="label">
        <i class="el-icon-tickets"></i>
        SUV Services
      </template>
      <span v-for="s in booking.suvServices" :key="s.index">{{ s }}</span>
    </el-descriptions-item>
    <el-descriptions-item v-if="booking.vanServices.length!=0">
      <template slot="label">
        <i class="el-icon-tickets"></i>
        Van Services
      </template>
      <span v-for="s in booking.vanServices" :key="s.index">{{ s }}</span>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-date"></i>
        Time
      </template>
      {{ booking.date.substring(0,10) }} - - {{ showPeriod(booking.time) }}
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        Manage
      </template>
      <el-button type="danger" size="small" @click="deleteBooking(booking._id)">Delete</el-button>  
    </el-descriptions-item>
  </el-descriptions>
     </el-card>  
    </div>
   
    <div>
        <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="pageSize"
      layout="total, prev, pager, next"
      :total="bookings.length"
      :hide-on-single-page="true"
      >
    </el-pagination>
    </div> 
    </el-tab-pane>

    
    <el-tab-pane label="Retrive by date">
      <div>
        <div class="block">
        <!-- <span class="demonstration">Choose a Date</span> -->
        <el-date-picker
          v-model="date"
          type="date"
          placeholder="Choose a Date">
        </el-date-picker>
  </div>
      </div>

      <div v-if="bookings.length!=0" >    
    <el-card>
        <el-descriptions class="margin-top"  :column="3"  v-for="booking in bookings" :key="booking.index" border
        >
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-user"></i>
        Name
      </template>
      {{booking.firstName}}{{booking.lastName}}
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-mobile-phone"></i>
        Phone Number
      </template>
      {{booking.phoneNumber}}
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-location-outline"></i>
        Address
      </template>
      {{ booking.address }}
    </el-descriptions-item>
    <el-descriptions-item v-if="booking.sedanServices.length!=0">
      <template slot="label">
        <i class="el-icon-tickets"></i>
        Sedan Services
      </template>
      <span v-for="s in booking.sedanServices" :key="s.index">{{ s }}</span>
    </el-descriptions-item>
    <el-descriptions-item v-if="booking.suvServices.length!=0">
      <template slot="label">
        <i class="el-icon-tickets"></i>
        SUV Services
      </template>
      <span v-for="s in booking.suvServices" :key="s.index">{{ s }}</span>
    </el-descriptions-item>
    <el-descriptions-item v-if="booking.vanServices.length!=0">
      <template slot="label">
        <i class="el-icon-tickets"></i>
        Van Services
      </template>
      <span v-for="s in booking.vanServices" :key="s.index">{{ s }}</span>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-date"></i>
        Time
      </template>
      {{ booking.date.substring(0,10) }} - - {{ showPeriod(booking.time) }}
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        Manage
      </template>
      <el-button type="danger" size="small" @click="deleteBooking(booking._id)">Delete</el-button>  
    </el-descriptions-item>
  </el-descriptions>
     </el-card>  
    </div>
    </el-tab-pane>
    
    <el-tab-pane label="Search">

      
        <div style="margin-top: 15px;">
          <el-input  v-model="input" class="input-with-select">
            <el-select class="select" v-model="select" slot="prepend" placeholder="Choose" >
              <el-option label="Email" value="1"></el-option>
              <el-option label="Phone" value="2"></el-option>
         
            </el-select>
            <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
          </el-input>
      </div>

      <div v-if="bookings.length!=0" >    
    <el-card>
        <el-descriptions class="margin-top"  :column="3"  v-for="booking in bookings" :key="booking.index" border
        >
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-user"></i>
        Name
      </template>
      {{booking.firstName}}{{booking.lastName}}
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-mobile-phone"></i>
        Phone Number
      </template>
      {{booking.phoneNumber}}
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-location-outline"></i>
        Address
      </template>
      {{ booking.address }}
    </el-descriptions-item>
    <el-descriptions-item v-if="booking.sedanServices.length!=0">
      <template slot="label">
        <i class="el-icon-tickets"></i>
        Sedan Services
      </template>
      <span v-for="s in booking.sedanServices" :key="s.index">{{ s }}<br></span>
    </el-descriptions-item>
    <el-descriptions-item v-if="booking.suvServices.length!=0">
      <template slot="label">
        <i class="el-icon-tickets"></i>
        SUV Services
      </template>
      <span v-for="s in booking.suvServices" :key="s.index">{{ s }}<br></span>
    </el-descriptions-item>
    <el-descriptions-item v-if="booking.vanServices.length!=0">
      <template slot="label">
        <i class="el-icon-tickets"></i>
        Van Services
      </template>
      <span v-for="s in booking.vanServices" :key="s.index">{{ s }}<br></span>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-date"></i>
        Time
      </template>
      {{ booking.date.substring(0,10) }} - - {{ showPeriod(booking.time) }}
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        Manage
      </template>
      <el-button type="danger" size="small" @click="deleteBooking(booking._id)">Delete</el-button>  
    </el-descriptions-item>
  </el-descriptions>
     </el-card>  
    </div>
    
      
    </el-tab-pane>
    
    <el-tab-pane label="Add">
      <el-button type="primary" @click="add">Click this button to add a booking </el-button>

    </el-tab-pane>
  </el-tabs>
  
    
</div>
</template>

<script>
// import { relativeTimeThreshold } from 'moment'
var moment = require('moment');

export default {
data() {
return {
    month:'',
    date:'',
    bookings:[],
    pageSize:10,
    currentPage:1,
    visible:false,
    input:'',
    select:''


}
},

methods:{
    // test(){
    //     console.log(this.month.getMonth(),this.month.getFullYear())
    // },
    handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },

      changeVisibility(){
        this.visible=false
      },

      handleClick() {
        this.bookings=[]
        this.month=''
        this.date=''

      },

    showPeriod(period){
            var label=""
            switch(period){
                    case 0:
                        label="7:30"
                        break
                    case 1:
                        label="8:30"
                        break
                    case 2:
                        label="9:30"
                        break
                    case 3:
                        label="10:30"
                        break
                    case 4:
                        label="11:30"
                        break
                    case 5:
                        label="12:30"
                        break
                    case 6:
                        label="13:30"
                        break
                    case 7:
                        label="14:30"
                        break
                    case 8:
                        label="15:30"
                        break
                    case 9:
                        label="16:30"
                        break
                    case 10:
                        label="17:30"
                        break
                }
                return label
        },

    getBookingsByMonths(){
      if(this.month!=''){
        this.$http.post('/getBookingsByMonths',{month:this.month.getMonth(),year:this.month.getFullYear()})
        .then((res)=>{
            if(res.data.flag){
                this.bookings= res.data.bookings
                // console.log(this.bookings)
            }else{
                console.log(res.data.massage)
                this.bookings=[]
            }
        })
      }  
     
    },

    getBookingsByDates(){
        if(this.date!=''){
          this.$http.post('/getBookingsByDates',{date:moment(this.date).format("YYYY-MM-DD")})
        .then((res)=>{
            if(res.data.flag){
              let bookings=res.data.bookings.filter((booking)=>booking.delete!=true)
              this.bookings= bookings
                // console.log(res.data.bookings)
            }else{
                // console.log(res.data.massage)
                this.bookings=[]
            }
          })
        }      
    },

    search(){
      if(this.select==1){
        this.$http.post('/getBookingsByEmail',{email:this.input})
        .then((res)=>{
          if(res.data.flag){
            let bookings=res.data.bookings.filter((booking)=>booking.delete!=true)
            this.bookings= bookings
          }else{
            console.log(res.data.massage)
            this.bookings=[]
          }
        })
        
      }else{
        this.$http.post('/getBookingsByPhone',{phone:this.input})
        .then((res)=>{
          if(res.data.flag){
            let bookings=res.data.bookings.filter((booking)=>booking.delete!=true)
            this.bookings= bookings
          }else{
            console.log(res.data.massage)
            this.bookings=[]
          }
        })
      }
    },

    add(){
      this.$router.push({
      path:"/booking1",
      })
    },

    deleteBooking(id){
        this.$confirm('Are you sure?', 'Reminder', {
          confirmButtonText: 'Confirm',
          cancelButtonText: 'Cancle',
          type: 'warning',
          center: true
        }).then(() => { 
            console.log(id)
            this.$http.post('/deleteBooking',{id:id}) 
            .then((res) => {
                if(res.data.flag){
                    this.$message({
                        type:'success',
                        message:'Delete successfully'
                    })
                    this.getBookingsByMonths()
                }else{
                    this.$message({
                        type:'erro',
                        message:'Delete unsuccessfully'
                    })
                }
            })    
      })
        .catch(() => {
          this.$message({
            type: 'info',
            message: 'Undeleted'
          });
        });
    }

   
},

watch:{
        month:function () {
            this.getBookingsByMonths()
            
        },

        date:function() {
          this.getBookingsByDates()
        }
    },

computed: {
    displayedItems() {
      // 根据分页参数筛选出当前页要展示的数据
      let bookings=this.bookings.filter((booking)=>booking.delete!=true)
      const startIndex = (this.currentPage - 1) * this.pageSize;
      const endIndex = startIndex + this.pageSize;
      return bookings.slice(startIndex, endIndex);
    },
  },

//生命周期 - 创建完成（访问当前this实例）
created() {


},
//生命周期 - 挂载完成（访问DOM元素）
mounted() {

}
}
</script>
<style scoped>
/* @import url(); 引入css类 */
.el-card{
 width: 70%;
 margin: 0 auto;
 display: flex;
  justify-content: center;

}
.el-descriptions{
    padding-top: 30px;
}

 .select{
  width: 100px ;
 }
 .input-with-select{
  width:500px;
 }
/* .el-select .el-input {
    width: 300px;
  } */
  .input-with-select .el-input-group__prepend {
    background-color: #fff;
  }

</style>