import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../components/Home.vue"
import Step1 from "../components/Booking/Step1.vue"
import Step2 from "../components/Booking/Step2.vue"
import Step3 from "../components/Booking/Step3.vue"
import AdminHome from "../components/Admin/AdminHome.vue"
import ManageBookings from "../components/Admin/ManageBookings.vue"

Vue.use(VueRouter);

const router=new VueRouter({
    mode:"history",
    base: process.env.BASE_URL,
    routes:[
        {
            path:"/",
            redirect:"/index",
        },
        {
            path:"/index",
            component: Home,
        },
        {
            path:"/booking1",
            component: Step1
        },
        {
            path:"/booking2",
            component: Step2
        },
        {
            path:"/booking3",
            component: Step3
        },
        {
            path:"/adminadmin",
            component: AdminHome
        },
        {
            path:"/manageBookings",
            component: ManageBookings
        }


    ]
}
);

export default router;
    
