<template>
    <header class="head-nav" :class="navBarFixed == true ? 'navBarWrap' :''">       
        <el-row>
            <el-col :span="6" class="logo-container">
                <div class="logo"><img src="../assets/HAO_S-GARAGE.svg" class="logo" alt="" /></div>
            
            </el-col>
            <router-link to="/booking1">
              <el-col :span="6" class="booking">
                  <!-- <span class="title">Make Appointment </span>  -->
                  <el-link type="primary" class="link"><i class="el-icon-s-order"></i>Book</el-link>
              </el-col>
            </router-link>
        </el-row>
    </header>
</template>

<script>
export default{
    name:"head-nav",
    data() {
    return {
        navBarFixed: false,
    };
  },
 
  components: {},
 
  mounted() {
    window.addEventListener("scroll", this.watchScroll);
  },
 
  methods: {
    watchScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
        // console.log(scrollTop)
      //  当滚动超过 90 时，实现吸顶效果
      if (scrollTop > 5) {
        this.navBarFixed = true;
      } else {
        this.navBarFixed = false;
      }
    }
  }

    
}
</script>
    
<style>
.head-nav {
  top:0;
  left:0; 
  width: 100%;
  height: 60px;
  min-width: 200px;
  padding: 5px;
  background: #fff;
  color: #fff;
  /* border-bottom: 1px solid #002fa7; */
  box-shadow: 10px 10px 5px #888888;
  margin:0;
}

.logo-container {
  line-height: 60px;
  /* max-width: 100px; */
}

.logo{
  width: 10rem;
   height: 10rem;
   max-height: 60px;
   /* vertical-align: -0.15em; */
   fill: currentColor;
   /* overflow: hidden; */
}


.navBarWrap {
    position:fixed;
    top:0;
    z-index:999;
  }

.title {
  vertical-align: middle;
  font-size: 0.5rem;
  font-family: "Microsoft YaHei";
  letter-spacing: 3px;
}

.booking {

  text-align: right;
  float: right;
  padding-right: 30px;
  line-height: 60px;
}

.link{
  font-size: 1rem;
}

.el-dropdown-link {
    cursor: pointer;
    color: #409EFF;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }
</style>