<template >
    <div>
        <HeadNav></HeadNav>
        <el-page-header @back="goBack" content="Please choose your booking time">
        </el-page-header>
    
        <div  class="steps" >
            <el-steps :active="2">
                <el-step title="Step 1" description="Choose services"></el-step>
                <el-step title="Step 2" description="Choose the booking time"></el-step>
                <el-step title="Step 3" description="Submit your information"></el-step>
             </el-steps>
        </div>
        <!-- <ul>
            <li v-for="item in checkboxGroup1" :key="item" >{{item}}</li>
        </ul> -->
        <!-- <el-calendar v-model="bookingTime" :disabled-date="disabledDate">
            <template
                slot="dateCell"
                slot-scope="{data}">
                <p :class="data.isSelected ? 'is-selected' : ''">
                
                {{ data.day.split('-').slice(2).join('-') }}{{ data.isSelected ? '✔️' : ''}}
                </p>
            </template>
        </el-calendar> -->

        
            <!-- <span class="demonstration">默认</span> -->
        <el-card>
            <el-date-picker
            v-model="bookingTime"
            type="date"
            placeholder="Choose the date"
            :picker-options="pickerOptions">
            </el-date-picker>
       

        <el-radio v-show="ifThisDayAvailable" v-model="chosenTime" v-for="a in availableTime" :label="a" :key="a" style="display:block"  border>{{showPeriod(a)}} </el-radio>
        <span v-show="!ifThisDayAvailable">No available time</span>
        <div id="submitBtn" >
             <el-button @click="submit" type="primary">Continue</el-button>
        </div>
       

    </el-card>




    </div>

</template>

<script>
 var moment = require('moment');
 import HeadNav from "../HeadNav.vue"

export default{
    name:"step-two",
    components: {
    HeadNav,
  },
    data(){
       return{
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() < Date.now();
          }
        },
        checkboxGroup1:[],
        checkboxGroup2:[],
        checkboxGroup3:[],
        ifThisDayAvailable:true,
        availableTime:[],
        chosenTime:-1,
        totalTime:0,
        bookingTime: new Date(),
        
        // bookingTime:moment(new Date()).format("YYYY-MM-DD")
       
       }
    },

    methods:{
        goBack(){
            this.$router.go(-1)
        },
        
        disabledDate(date){
            return date < new Date();
        },
        
        showPeriod(period){
            var label=""
            switch(period){
                    case 0:
                        label="7:30"
                        break
                    case 1:
                        label="8:30"
                        break
                    case 2:
                        label="9:30"
                        break
                    case 3:
                        label="10:30"
                        break
                    case 4:
                        label="11:30"
                        break
                    case 5:
                        label="12:30"
                        break
                    case 6:
                        label="13:30"
                        break
                    case 7:
                        label="14:30"
                        break
                    case 8:
                        label="15:30"
                        break
                    case 9:
                        label="16:30"
                        break
                    case 10:
                        label="17:30"
                        break
                }
                return label
        },
        getAvailableTime(){
            console.log(this.bookingTime,this.totalTime)
            this.$http.post('/getAvailableTime',
            {               
                date:moment(this.bookingTime).format("YYYY-MM-DD"),
                totalTime:this.totalTime
            }
            )
            .then((res) => {
                if (res.data.flag) {
                  this.$message.success(res.data.message);
                  this.ifThisDayAvailable=res.data.available
                  this.availableTime=res.data.availableTime
                } else {
                  this.$message.error(res.data.message);
                }
            });
        },

        submit(){
            
            if(this.chosenTime!==-1){
                localStorage.removeItem('chosenTime')
                localStorage.removeItem('chosenDate')
                localStorage.removeItem('totolTime')
                
                localStorage.setItem('chosenTime',JSON.stringify(this.chosenTime))
                localStorage.setItem('chosenDate',JSON.stringify(moment(this.bookingTime).format("YYYY-MM-DD")))
                localStorage.setItem('totolTime',JSON.stringify(this.totalTime))
                
                this.$router.push("/booking3");

            }else{
                this.$message.error("Sorry, you haven't chosen the time.");
            }

        }
    },

    created(){
        this.checkboxGroup1=JSON.parse(localStorage.getItem('checkboxGroup1'))
        this.checkboxGroup2=JSON.parse(localStorage.getItem('checkboxGroup2'))
        this.checkboxGroup3=JSON.parse(localStorage.getItem('checkboxGroup3'))
        this.totalTime=Number(localStorage.getItem('totalTime'))

    },

    mounted(){
        this.getAvailableTime()

    },

    watch:{
        bookingTime:function () {
           
            console.log("time changed",this.bookingTime)
            this.getAvailableTime()

        }
    },
    }
</script > 

<style scoped>
 /* .is-selected {
    color: #1989FA;
  } */


.el-card{
 width: 70%;
 margin: 0 auto;
 display: flex;
  justify-content: center;

}

.el-radio{
    /* display: flex;
   justify-content: center; */
    /* width: 0%; */
    margin-top: 10px;
    white-space: normal;
    height: auto;
    word-break:break-all;
    word-wrap:break-word;
}

#submitBtn{
    font-size: 2rem;
    margin-top: 10px;
}


</style>