<template>
    <div>
        <HeadNav></HeadNav>
        <el-page-header @back="goBack" content="Please choose services you want">
        </el-page-header>
        <div  class="steps" >
            <el-steps :active="1">
                <el-step title="Step 1" description="Choose services"></el-step>
                <el-step title="Step 2" description="Choose the booking time"></el-step>
                <el-step title="Step 3" description="Submit your information"></el-step>
             </el-steps>
        </div>
   
    <el-card class="card">
    <el-tabs  type="card" @tab-click="handleClick" v-model="activeName" >
        <el-tab-pane label="Sedan & Hatchback" name="first">
            <el-checkbox-group v-model="checkboxGroup1" >
                    <el-checkbox v-for="s in services1" :label="s.name" :key="s.id" style="display:block" border>{{s.name}}--{{s.time}}h--${{s.price}}</el-checkbox>
         </el-checkbox-group>
        </el-tab-pane>
        
        <el-tab-pane label="SUV & UTE" name="second">
            <el-checkbox-group v-model="checkboxGroup2" >
                    <el-checkbox v-for="s in services2" :label="s.name" :key="s.id" style="display:block" border>{{s.name}}--{{s.time}}h--${{s.price}}</el-checkbox>
         </el-checkbox-group>
        </el-tab-pane>
        
        <el-tab-pane label="7-SEATER, VAN & LIGHT TRUCK" name="third">
            <el-checkbox-group v-model="checkboxGroup3" >
                    <el-checkbox v-for="s in services3" :label="s.name" :key="s.id" style="display:block" border>{{s.name}}--{{s.time}}h--${{s.price}}</el-checkbox>
         </el-checkbox-group>
        </el-tab-pane>

        <!-- <el-calendar v-model="bookingTime"></el-calendar> -->
        <!-- <el-tab-pane v-for="type in types" :label="type" :name="type" :key="type">
             <el-checkbox-group v-model="checkboxGroup" >
                    <el-checkbox v-for="s in services" :label="s" :key="s" style="display:block" >{{s}}</el-checkbox>
         </el-checkbox-group>
    </el-tab-pane> -->
  </el-tabs>
  <el-button @click="submit" type="primary" id="submitBtn">Continue</el-button>
</el-card>
</div>  

</template>

<script>
const typeOptions=['Sedan & Hatchback','SUV & UTE','7-SEATER, VAN & LIGHT TRUCK ']
import HeadNav from "../HeadNav.vue"
export default{
    name:"step-one",
    components: {
    HeadNav,
  },
    data(){
    
        return{
            // tabPosition:"left",
            activeName:"first",
            type:['Sedan'],
            checkboxGroup1:[],
            checkboxGroup2:[],
            checkboxGroup3:[],
            // bookingTime: new Date(),

            // service:[{service:'1',time:90,price:100}],
            types: typeOptions,
            services:["Regular Detailing","Interior Detailing","Exterior Detailing","Full Detailing","Pre-sale Detailing or usedcar buyer’s option"],
            services1:[
                {id:1,name:"Regular Detailing",price:"100",time:1},
                {id:2,name:"Interior Detailing",price:"200",time:2},
                {id:3,name:"Exterior Detailing",price:"200",time:2},
                {id:4,name:"Full Detailing",price:"270",time:3.5},
                {id:5,name:"Pre-sale Detailing or usedcar buyer’s option",price:"350",time:4},
            ],
            services2:[
                {id:1,name:"Regular Detailing",price:"110",time:1},
                {id:2,name:"Interior Detailing",price:"220",time:2},
                {id:3,name:"Exterior Detailing",price:"220",time:2},
                {id:4,name:"Full Detailing",price:"310",time:3.5},
                {id:5,name:"Pre-sale Detailing or usedcar buyer’s option",price:"400",time:4.5},
            ],
            services3:[
                {id:1,name:"Regular Detailing",price:"120",time:1.5},
                {id:2,name:"Interior Detailing",price:"240",time:2.5},
                {id:3,name:"Exterior Detailing",price:"240",time:2.5},
                {id:4,name:"Full Detailing",price:"350",time:4},
                {id:5,name:"Pre-sale Detailing or usedcar buyer’s option",price:"450",time:5}
            ]
            
            
        };
    },

    methods: {
     goBack(){
            this.$router.go(-1)
        },
        
      handleClick(tab) {
        console.log(tab.name);
      },

      submit(){
        localStorage.clear()
        if(this.totalTime!=0){
            localStorage.setItem('checkboxGroup1',JSON.stringify(this.checkboxGroup1))
            localStorage.setItem('checkboxGroup2',JSON.stringify(this.checkboxGroup2))
            localStorage.setItem('checkboxGroup3',JSON.stringify(this.checkboxGroup3))
            localStorage.setItem('totalTime',this.totalTime)
            this.$router.push("/booking2");
        }else{
            this.$message.error("Sorry, you haven't chosen any item.");
        }      
      }
    },

    computed:{
        totalTime(){
            var time=0
            for(let i in this.checkboxGroup1){
                switch(this.checkboxGroup1[i]){
                    case "Regular Detailing":
                        time=time+1
                        break
                    case "Interior Detailing":
                        time=time+2
                        break
                    case "Exterior Detailing":
                        time=time+2
                        break
                    case "Full Detailing":
                        time=time+3.5
                        break
                    case "Pre-sale Detailing or usedcar buyer’s option":
                        time=time+4
                        break
                }
            }
            for(let i in this.checkboxGroup2){
                switch(this.checkboxGroup2[i]){
                    case "Regular Detailing":
                        time=time+1
                        break
                    case "Interior Detailing":
                        time=time+2
                        break
                    case "Exterior Detailing":
                        time=time+2
                        break
                    case "Full Detailing":
                        time=time+3.5
                        break
                    case "Pre-sale Detailing or usedcar buyer’s option":
                        time=time+4
                        break
                }
            }
            for(let i in this.checkboxGroup3){
                switch(this.checkboxGroup3[i]){
                    case "Regular Detailing":
                        time=time+1.5
                        break
                    case "Interior Detailing":
                        time=time+2.5
                        break
                    case "Exterior Detailing":
                        time=time+2.5
                        break
                    case "Full Detailing":
                        time=time+4
                        break
                    case "Pre-sale Detailing or usedcar buyer’s option":
                        time=time+5
                        break
                }
            }
            return time
        }
    }

}
</script>

<style>
.steps{
    /* display:flex;
    justify-content:center; */
    margin:auto;
    width: 50%;
    text-align: left;
    padding: 20px;
}

.card{
 width: 70%;
 margin: 0 auto;
 display: flex;
  justify-content: center;
}

.el-tabs__header {
  display: flex;
  justify-content: center;
}

#submitBtn{
    font-size: 1.5rem;
    margin-top: 10px;
}

/* .el-checkGroup{
    display: flex;
    justify-content: center;
} */

.el-checkbox{
    /* display: flex;
   justify-content: center; */
    /* width: 0%; */
    margin-top: 10px;
    white-space: normal;
    height: auto;
    word-break:break-all;
    word-wrap:break-word;
}

</style>